import * as React from 'react'
import { Link } from 'react-router-dom';

export default function WelcomePageText() {
  return (
    <div>
      <h1>Welcome to Hangman!</h1>
      <Link to={`${process.env.PUBLIC_URL}/hangman`}><button type="button">Start playing!</button></Link>
      <Link to={`${process.env.PUBLIC_URL}/history`}><button type="button">Read about the History!</button></Link>
    </div>
  )
}


